.chat__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.chat_body{
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 400px;
    text-align: center;
    border: 2px solid black;
}

.textInput{
    width: 450px;
    height: 50px;
    background-color: rgb(255, 205, 205);
}

.messagesContainer{
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}